import request from '@/utils/request'

//
function overviewCount(){
    return request({
        url: '/overview/count',
        method: 'get'
    })
}
//
function registerChart(params){
    return request({
        url: '/overview/registerChart',
        method: 'get',
        params
    })
}
//
function rechargeChart(params){
    return request({
        url: '/overview/rechargeChart',
        method: 'get',
        params
    })
}
export default {
    overviewCount,
    registerChart,
    rechargeChart
}