import request from '@/utils/request'

// 
function planListData(params) {
  return request({
    url: '/cronWorker/list',
    method: 'get',
    params: params
  })
}

//
function addPlan(data) {
  return request({
    url: '/cronWorker/insert',
    method: 'post',
    data
  })
}

// 
function planDetail(params) {
  return request({
    url: '/cronWorker/detail',
    method: 'get',
    params: params
  })
}

//
function updatePlan(data) {
  return request({
    url: '/cronWorker/update',
    method: 'post',
    data
  })
}

//
function deletePlan(data) {
  return request({
    url: '/cronWorker/delete',
    method: 'post',
    data
  })
}
//
function runningPlan(data) {
  return request({
    url: '/cronWorker/running',
    method: 'post',
    data
  })
}
export default {
  planListData,
  addPlan,
  planDetail,
  updatePlan,
  deletePlan,
  runningPlan
}