import request from '@/utils/request'

// app
function appListData(params) {
  return request({
    url: '/app/list',
    method: 'get',
    params: params
  })
}

//
function addApp(data) {
  return request({
    url: '/app/insert',
    method: 'post',
    data
  })
}

// app
function appDetail(params) {
  return request({
    url: '/app/detail',
    method: 'get',
    params: params
  })
}

//
function updateApp(data) {
  return request({
    url: '/app/update',
    method: 'post',
    data
  })
}

//
function deleteApp(data) {
  return request({
    url: '/app/delete',
    method: 'post',
    data
  })
}

// 
function recoveryApp(data) {
  return request({
    url: '/app/recovery',
    method: 'post',
    data
  })
}

// 
function realDeleteApp(data) {
  return request({
    url: '/app/realDelete',
    method: 'post',
    data
  })
}

// button
function buttonListData(params) {
  return request({
    url: '/app/button/list',
    method: 'get',
    params: params
  })
}
// button
function buttonDetail(params) {
  return request({
    url: '/app/button/detail',
    method: 'get',
    params: params
  })
}
//button
function deleteButton(data) {
  return request({
    url: '/app/button/delete',
    method: 'post',
    data
  })
}
//button
function updateButton(data) {
  return request({
    url: '/app/button/update',
    method: 'post',
    data
  })
}
//button
function insertButton(data) {
  return request({
    url: '/app/button/insert',
    method: 'post',
    data
  })
}
// 
function recoveryButton(data) {
  return request({
    url: '/app/button/recovery',
    method: 'post',
    data
  })
}
// 
function realDeleteButton(data) {
  return request({
    url: '/app/button/realDelete',
    method: 'post',
    data
  })
}

// 
function getuploadFile(data) {
  return request({
    url: '/file/uploadFile',
    method: 'post',
    data
  })
}
//  
function getfileSts(params) {
  return request({
    url: '/file/sts',
    method: 'get',
    params: params,
  })
}
export default {
  appListData,
  addApp,
  appDetail,
  updateApp,
  deleteApp,
  buttonListData,
  buttonDetail,
  deleteButton,
  updateButton,
  insertButton,
  recoveryApp,
  recoveryButton,
  realDeleteApp,
  realDeleteButton,
  getuploadFile,
  getfileSts
}
