import request from '@/utils/request'

// 
function ListData(params) {
  return request({
    url: '/gift/list',
    method: 'get',
    params: params
  })
}
// 
function GiftInsert(data) {
  return request({
    url: '/gift/insert',
    method: 'post',
    data
  })
}
// 
function GiftUpdate(data) {
  return request({
    url: '/gift/update',
    method: 'post',
    data
  })
}
// 
function GiftDelete(data) {
  return request({
    url: '/gift/delete',
    method: 'post',
    data
  })
}
// 
function GiftRecovery(data) {
  return request({
    url: '/gift/recovery',
    method: 'post',
    data
  })
}
//
function HistoryData(params) {
  return request({
    url: '/gift/history',
    method: 'get',
    params: params
  })
}
// 
function GiftsetSort(data) {
  return request({
    url: '/gift/setSort',
    method: 'post',
    data
  })
}
export default {
  ListData,
  GiftInsert,
  GiftUpdate,
  GiftDelete,
  HistoryData,
  GiftRecovery,
  GiftsetSort
}