import request from '@/utils/request'

export function login(data) {
  return request({
    url: '/login/account',
    method: 'post',
    data
  })
}
// 
export function resetPsw(data) {
  return request({
    url: '/login/forget',
    method: 'post',
    data
  })
}
// 
export function phoneLogin(data) {
  return request({
    url: '/login/phonelogin',
    method: 'post',
    data
  })
}
// --
export function getCaptcha(data) {
  return request({
    url: '/sms/send',
    method: 'post',
    data
  })
}
// 
export function getInfo(token) {
  return request({
    url: '/admin/info/',
    method: 'get',
    params: { token }
  })
}
// 
export function logout() {
  return request({
    url: '/login/logout',
    method: 'get'
  })
}
// 
export function getStaffmenu() {
  return request({
    url: '/staff/menu/allmenu',
    method: 'get'
  })
}
// 
export function getStaffInfo() {
  return request({
    url: '/staff/menu/info',
    method: 'get'
  })
}

