import request from '@/utils/request'

//List
function deptData(params) {
  return request({
    url: '/admin/dept/list',
    method: 'get',
    params: params
  })
}
// 
function deptInsert(data) {
  return request({
    url: '/admin/dept/insert',
    method: 'post',
    data
  })
}
// 
function deptUpdate(data) {
  return request({
    url: '/admin/dept/update',
    method: 'post',
    data
  })
}
// 
function deptDelete(data) {
  return request({
    url: '/admin/dept/delete',
    method: 'post',
    data
  })
}
