import request from '@/utils/request'

// 
function configList(params) {
  return request({
    url: '/system/config/list',
    method: 'get',
    params
  })
}
// 
function configUpdate(data) {
  return request({
    url: '/system/config/update',
    method: 'post',
    data
  })
}
// 
