import request from '@/utils/request'

// 
function customerData(params) {
  return request({
    url: '/customer/',
    method: 'get',
    params: params
  })
}
//
function customerDetail(params) {
  return request({
    url: '/customer/detail',
    method: 'get',
    params: params
  })
}
export default {
  customerData,
  customerDetail
}