import request from '@/utils/request'

//
function raiseEnroll(params) {
    return request({
        url: '/crow/enroll/list',
        method: 'get',
        params
    })
}
//
function EnrollDelete(data) {
    return request({
        url: '/crow/enroll/delete',
        method: 'post',
        data
    })
}
//
function raiseList(params) {
    return request({
        url: '/crow/list',
        method: 'get',
        params
    })
}
//
function raiseDelete(data) {
    return request({
        url: '/crow/delete',
        method: 'post',
        data
    })
}
//
function raiseRecovery(data) {
    return request({
        url: '/crow/recovery',
        method: 'post',
        data
    })
}
//
function raiseUpdate(data) {
    return request({
        url: '/crow/update',
        method: 'post',
        data
    })
}
//
function groupChatData(params) {
    return request({
        url: '/crow/groupAdminInfo',
        method: 'get',
        params
    })
}
export default {
    raiseList,
    raiseDelete,
    raiseUpdate,
    raiseEnroll,
    EnrollDelete,
    raiseRecovery,
    groupChatData
}