import request from '@/utils/request'

// 
function threadlist(params) {
  return request({
    url: '/community/threadlist',
    method: 'get',
    params: params
  })
}
// 
function commentlist(params) {
  return request({
    url: '/community/commentlist',
    method: 'get',
    params: params
  })
}
// 
function commentreply(params) {
  return request({
    url: '/community/commentreply',
    method: 'get',
    params: params
  })
}
// 
function threaddetail(params) {
  return request({
    url: '/community/threaddetail',
    method: 'get',
    params: params
  })
}
// 
function GiftInsert(data) {
  return request({
    url: '/community/handling',
    method: 'post',
    data
  })
}
// 
function tuijieSet(data) {
  return request({
    url: '/community/set',
    method: 'post',
    data
  })
}
// 
function delHandles(data) {
  return request({
    url: '/community/delete',
    method: 'post',
    data
  })
}

//
function importList(params) {
  return request({
    url: '/community/import/list',
    method: 'get',
    params: params
  });
}
//
function importAdd(data) {
  return request({
    url: '/community/import/add',
    method: 'post',
    data
  });
}

//
function dyimportList(params) {
  return request({
    url: '/community/dyimport/list',
    method: 'get',
    params: params
  });
}
//
function dyimportAdd(data) {
  return request({
    url: '/community/dyimport/add',
    method: 'post',
    data
  });
}

//
function communityModify(data) {
  return request({
    url: '/community/modify',
    method: 'post',
    data
  });
}
//
function communityAudit(data) {
  return request({
    url: '/community/commentHandling',
    method: 'post',
    data
  });
}
//
function onlyCommunity(params) {
  return request({
    url: '/community/commentOnly',
    method: 'get',
    params: params
  });
}
export default {
  threadlist,
  commentlist,
  commentreply,
  threaddetail,
  GiftInsert,
  tuijieSet,
  delHandles,
  importList,
  importAdd,
  dyimportList,
  dyimportAdd,
  communityModify,
  communityAudit,
  onlyCommunity
}
